import React from 'react'
import { Button, Card, Container, Row, Col, Alert} from 'react-bootstrap'
export default class EmployeeEdit extends React.Component{
    constructor(){
        super()
        this.state={employee:[], alertVisible:false, alertColor:"success", alertMessage:""}
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    handleSubmit(e){
        e.preventDefault()
        const form = document.forms.employeeUpdate
        const id = form.id.value
        const name = form.name.value
        const extension = form.extension.value
        const email = form.email.value
        const title = form.title.value
        const currentlyEmployed = form.currentlyEmployed.checked

        const url = `/api/employees/${id}`
        fetch(url, {
            method: 'PATCH',
            headers:{
                'Accept': 'application/json',
                "Content-Type":'application/json;charset=UTF-8',
            },
            body:JSON.stringify(
                {
                 "name" : name,
                 "email" : email,
                 "extension" : extension,
                 "title" : title,
                 "currentlyEmployed" : currentlyEmployed,
                }),
        })
        .then((response)=>response.json())
        .then(data=>{
            this.setState(
                {
                    alertVisible:true,
                    alertMessage:data.msg
                }
            )
        })
        console.log("Submitted")
    }

    componentDidMount(){
        this.loadData()
    }

    loadData(){
        const id = window.location.pathname.split('/')[2]
        fetch(`/api/employees/${id}`)
        .then(response=>response.json())
        .then((data)=>{
            data.employee.dateHired = new Date(data.employee.dateHired)
            this.setState({employee:data.employee})
        })
        .catch((err)=>console.log(err))
    }
    
    render(){
        return (
            <Card>
            <Card.Header as="h5">Edit {this.state.employee.name}</Card.Header>
            <Card.Body>
              <Card.Text>
                    <Container>
                            <form name='employeeUpdate' onSubmit={this.handleSubmit}>
                            <h1>Edit {this.state.employee.name}</h1>
                            <Row>
                                <Col md={2}>ID:</Col>
                                <Col md="auto"> 
                                    <input type='text' name='id' defaultValue={this.state.employee._id} readOnly='readOnly'/>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={2}>Name:</Col>
                                <Col md="auto"><input type='text' name='name' defaultValue={this.state.employee.name}/></Col>
                            </Row>
                            <Row>
                                <Col md={2}>Extension</Col>
                                <Col md="auto"><input type='text' name='extension' defaultValue={this.state.employee.extension}/></Col>
                            </Row>
                            <Row>
                                <Col md={2}>Email:</Col>
                                <Col md="auto"><input type='text' name='email' defaultValue={this.state.employee.email}/></Col>
                            </Row>
                            <Row>
                                <Col md={2}>Title:</Col>
                                <Col md="auto"><input type='text' name='title' defaultValue={this.state.employee.title}/></Col>
                            </Row>
                            <Row>
                                <Col md={2}>Date Hired:</Col>
                                <Col md="auto"><input type='text' name='dateHired' readOnly='readOnly' defaultValue={this.state.employee.dateHired}/></Col>
                            </Row>
                            <Row>
                                <Col md={2}>Currently Employed?</Col>
                                <Col md="auto"> <input type='checkbox' name='currentlyEmployed' defaultChecked={this.state.employee.extension}/></Col>
                            </Row>
                            <Button type="submit" variant="primary" size="sm" className='my-3'>Update Employee</Button>
                            <Alert dismissible
                            variant={this.state.alertColor} show={this.state.alertVisible}
                            onClose={(e)=>this.setState({alertVisible:false})}
                            >{this.state.alertMessage}</Alert>
                        </form>
                    </Container>
            </Card.Text>
              </Card.Body>
        </Card>
        )
    }
  }
  